.adminMenuButtonContainer{
	width:100%;
	height:70px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex:1 1 0%;
	    flex:1 1 0%;
	-ms-flex-align: center;
	    align-items: center;
	padding-left: 20px;
	cursor: pointer;
	-webkit-transition:all 0.5s ease;
	-o-transition:all 0.5s ease;
	transition:all 0.5s ease;
}

.adminMenuButtonContainer p{
	font-size: 12px;
	color: #FFFFFF;
    font-weight: 600;
	letter-spacing: 1.71px;
	text-transform: uppercase;
	line-height: 50px;
}
.adminMenuButtonContainer.active{
	background:#1B2C2F;
}



