.checkbox{
	overflow: hidden;
	display: -ms-flexbox;
	display: flex;
	-ms-flex:1 1 0%;
	    flex:1 1 0%;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	position: relative;
	height:50px;
	margin:5px 5px 5px 5px;
	padding: 0 20px;
	border: 1px solid #f2f2f2;

}


.checkbox input {
     display: none;
}

.checkbox p {
	margin-right:10px;
}

.checkbox span {
	position: absolute;
	right:10px;
	top:15px;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    display: block;
    -webkit-box-shadow: 2px 2px 20px  #656565;
            box-shadow: 2px 2px 20px  #656565;
}

.checkbox input:checked + span {
    background: #f2f2f2;
}


