.NavBarButtonContainer{
	width:100%;
	height:70px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex:1 1 0%;
	    flex:1 1 0%;
	-ms-flex-align: center;
	    align-items: center;
	padding: 0 80px 0 0;
	cursor: pointer;
	-webkit-transition:all 0.5s ease;
	-o-transition:all 0.5s ease;
	transition:all 0.5s ease;
}

.NavBarButtonContainer h4{
	font-size: 12px;
    font-weight: 600;
	letter-spacing: 1.71px;
	text-transform: uppercase;
	line-height: 50px;
}
