.fileInput{
	display: none;
}

.fileUploadContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	position: relative;
}

.fileUploadingContainer{
	position: absolute;
	top:0px;
	bottom:0px;
	left:5px;
	right:5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	padding-left:5px;
	background: #1F3134;
	color:#fff;
	margin:5px 5px 5px;
}

.fileUploadingContainer p{
	margin:0;
	padding:0;
}

.fileUploadSpinner{
	height:50px;
	width:auto;
	position: absolute;
	right:5px;
	top:0;

}

.formFalseFileInput{
	background: #242424;
	color:#d8d8d8;
	height:50px;
	margin:5px;
	padding: 0px 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex:1.5 1 0%;
	    flex:1.5 1 0%;
	-ms-flex-align: center;
	    align-items: center;
}

.uploadButton{
	width:50px;
	font-family: FontAwesome;
	content: "\f093";
	background: #242424;
	color:#d8d8d8;
	height:50px;
	margin:5px;
	font-size: 20px;
	
	-ms-flex-align: center;
	
	    align-items: center;
}

.uploadButton::after{
		font-family: FontAwesome;
	content: "\f093";
display: -ms-flexbox;
display: flex;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: center;
    justify-content: center;
line-height: 50px;
}


