.adminSectionHeaderContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: row;
	    flex-direction: row;
	position: relative;
	padding:0;
	margin:70px 0 50px;
}

.adminSectionHeaderContainer h1{
	margin:0;
}

.addNewContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	position: absolute;
	top:0;
	right:0;
}

.addNewContainer div{
		display: -ms-flexbox;
		display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	font-size: 30px;
}

.addNewButton{
	width:50px;
	height:50px;
	background:#242424;
	color:white;
}




